"use strict";

var pubnub = new PubNub({
  subscribeKey: "sub-c-0b2aaa44-a779-11e6-be20-0619f8945a4f",
  publishKey: "pub-c-722b1270-6c2b-423d-98f6-cb21c18a2265"
});
jQuery(document).ready(function ($) {
  pubnub.subscribe({
    channels: ['NFC_TAG_CLIENT']
  });
  pubnub.addListener({
    message: function message(m) {
      var channelName = m.channel;
      console.log('message came in: ', m);
      console.log(channelName);
    }
  });
  $('input[name=media_type]').click(function (e) {
    var $this = $(e.currentTarget);

    if ($this.is(':checked')) {
      if ($this.attr('id') == 'media_type_solid') {
        $('#peripheral_type_group, #sealed_row').show();
        $('#syringe_group').hide();
        switchPlate();
        var table = $('.plate').filter(function (i, d) {
          return $(d).is(':visible');
        });
        table.find('input').eq(0).focus(); //$('.plate').is(':visible').find('input').eq(0).focus()
      } else {
        $('#peripheral_type_group, .form-row.plate, #sealed_row').hide();
        $('#syringe_group').show();
      }
    }
  });

  var switchPlate = function switchPlate() {
    $('.form-row.plate').hide();

    if ($('#media_type_solid').is(':checked')) {
      var plate = $('#peripheral_type').val();
      $('#' + plate).show();
    }
  };

  $('#peripheral_type').change(switchPlate).change();
  $('#submit_nfc_form').submit(function (e) {
    e.preventDefault();
    var experiment = {};
    var $this = $(e.currentTarget);
    var fields = ['id', 'client', 'location', 'start_time', 'duration', 'target_temperature', 'imaging_frequency'];

    for (var i = 0; i < fields.length; i++) {
      experiment[fields[i]] = $this.find('#' + fields[i]).val();
    }

    if ($this.find('input[name=media_type]:checked').val() == 'solid') {
      experiment.peripheral_type = $this.find('#peripheral_type').val();
      var load = {};
      var $plate = $this.find('.form-row.plate:visible').find('table');
      $plate.find('tr').each(function (i, row) {
        var chr = String.fromCharCode(65 + i);
        $(row).find('td').each(function (j, td) {
          load[chr + j] = $(td).find('input[type=text]').val();
        });
      });
      experiment.load = load;
    } else {
      experiment.syringe = $this.find('#syringe').val();
    }

    $('#json_sent').val(JSON.stringify(experiment));
    sendTag(experiment);
  });
});

var $c = function $c(t) {
  console.log(t);
};

var config = function config() {
  var config = {};

  if (window.location.hostname == '') {
    //this is for local testing with local MongoDB
    config.apiUrl = 'http://localhost:4001';
  } else {
    config.apiUrl = 'http://18.191.34.33:4000';
  }

  return config;
};

var sendTag = function sendTag(tagObj) {
  var message = {
    cmnd: 'ADD_NFC',
    data: tagObj
  };
  pubnub.publish({
    message: message,
    channel: 'SERVER',
    meta: getMeta()
  });
};

var getMeta = function getMeta() {
  return {
    'uuid': pubnub.getUUID(),
    'v': 1
  };
};
